'use client';
import React, { useState } from 'react'
import { motion } from 'framer-motion';
import Image from 'next/image';
import Bannerone from "@/asset/banner.png";
import Flight from "@/asset/1.svg";

const tabs = ['About Trivasia', 'Our Mission', 'Our Vision'];
function Home() {
    const [activeTab, setActiveTab] = useState('About Trivasia');
    return (
        <div>
            <section className="bg-[#fff8f3] pt-12 md:pt-20 pb-16">
                <div className="max-w-7xl mx-auto flex flex-col-reverse md:flex-row items-center px-6 gap-10">
                    {/* LEFT TEXT */}
                    <motion.div
                        className="md:w-1/2 text-center md:text-left"
                        initial={{ opacity: 0, y: 40 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.8 }}
                    >
                        <h1 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 leading-tight">
                            Your Gateway to <br />
                            <span className="text-red-600">Study</span>,{' '}
                            <span className="text-red-600">Holiday</span>, and <br />
                            <span className="text-red-600">New Beginnings!</span>
                        </h1>

                        <p className="mt-4 text-gray-600 text-base md:text-lg">
                            Explore Education, Enjoy Vacations, Build a New Life â€“ All Under One Roof
                        </p>

                        <motion.button
                            whileHover={{ scale: 1.05 }}
                            className="mt-6 bg-red-600 hover:bg-red-700 transition text-white px-6 py-3 rounded-full text-sm font-semibold"
                        >
                            Get Started
                        </motion.button>
                    </motion.div>

                    {/* RIGHT IMAGE */}
                    <motion.div
                        className="md:w-1/2 relative flex justify-center"
                        initial={{ opacity: 0, x: 40 }}
                        animate={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8, delay: 0.2 }}
                    >
                        <Image
                            src={Bannerone} // your image in public folder
                            alt="Student Travel Girl"
                            width={400}
                            height={400}
                            className="rounded-xl z-10 relative"
                        />

                        {/* Animated Plane Icon */}
                        <motion.div
                            className="absolute top-[-30px] right-[-20px]"
                            animate={{
                                y: [0, -10, 0],
                                x: [0, 10, 0],
                                rotate: [0, 5, 0],
                            }}
                            transition={{
                                duration: 3,
                                repeat: Infinity,
                                ease: 'easeInOut',
                            }}
                        >
                            <Image src={Flight} alt="Plane" width={40} height={40} />
                        </motion.div>
                    </motion.div>
                </div>
            </section>


            <section className="bg-white py-16 px-4 md:px-8">
                <div className="max-w-7xl mx-auto flex flex-col md:flex-row items-center gap-10">
                    {/* LEFT IMAGES */}
                    <motion.div
                        className="relative flex-1 grid grid-cols-2 gap-4"
                        initial={{ opacity: 0, x: -50 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8 }}
                    >
                        <div className="rounded-xl overflow-hidden">
                            <Image
                                src="/about-1.jpg"
                                alt="Graduation"
                                width={300}
                                height={400}
                                className="object-cover w-full h-auto"
                            />
                        </div>
                        <div className="rounded-xl overflow-hidden mt-10">
                            <Image
                                src="/about-2.jpg"
                                alt="Passport Couple"
                                width={300}
                                height={400}
                                className="object-cover w-full h-auto"
                            />
                        </div>
                        <div className="absolute bottom-[-20px] left-20 w-[180px] rounded-xl overflow-hidden shadow-lg">
                            <Image
                                src="/about-3.jpg"
                                alt="Reading Girls"
                                width={300}
                                height={200}
                                className="object-cover"
                            />
                        </div>

                        {/* Plane Animation */}
                        <motion.div
                            className="absolute -top-4 -left-6"
                            animate={{ y: [0, -10, 0], x: [0, 10, 0], rotate: [0, 5, 0] }}
                            transition={{ repeat: Infinity, duration: 4, ease: 'easeInOut' }}
                        >
                            <Image src="/plane-icon.png" alt="Plane" width={30} height={30} />
                        </motion.div>
                    </motion.div>

                    {/* RIGHT TEXT CONTENT */}
                    <motion.div
                        className="flex-1"
                        initial={{ opacity: 0, x: 50 }}
                        whileInView={{ opacity: 1, x: 0 }}
                        transition={{ duration: 0.8 }}
                    >
                        <p className="text-red-600 uppercase text-sm font-semibold tracking-widest">About Us</p>
                        <h2 className="text-2xl sm:text-3xl md:text-4xl font-bold text-gray-900 leading-snug mt-2">
                            Your Gateway to Global Experiences: <span className="text-red-600">Unveiling Trivasia</span>
                        </h2>

                        {/* Tabs */}
                        <div className="flex flex-wrap gap-4 mt-6 mb-4 text-sm sm:text-base font-medium text-gray-600">
                            {tabs.map(tab => (
                                <button
                                    key={tab}
                                    onClick={() => setActiveTab(tab)}
                                    className={`border-b-2 pb-1 transition ${activeTab === tab ? 'border-red-600 text-red-600' : 'border-transparent hover:text-red-600'
                                        }`}
                                >
                                    {tab}
                                </button>
                            ))}
                        </div>

                        {/* Tab Content */}
                        <p className="text-gray-600 text-sm sm:text-base mb-6 leading-relaxed">
                            Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed qu...
                        </p>

                        {/* Features List */}
                        <div className="space-y-3">
                            <div className="flex items-center gap-3 text-gray-800">
                                <CheckCircle className="w-5 h-5 text-red-600" />
                                <span>100% Graduation Assistance</span>
                            </div>
                            <div className="flex items-center gap-3 text-gray-800">
                                <CheckCircle className="w-5 h-5 text-red-600" />
                                <span>Financial Support</span>
                            </div>
                        </div>
                    </motion.div>
                </div>
            </section>
        </div>
    )
}

export default Home
